:root {
  --first-color: #488cff;
  --second-color: #ffff8c;
}

.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

#header {
    z-index: 999;
    width: 100%;
    background-color: #4A76A8;
    text-align: left;
    top: 0px;
    overflow: hidden;
    position: -webkit-sticky;
    position: sticky;
}

.row {
    display: flex;
    align-items: center;
    justify-content: center;
}

.col-md-12 {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}
